import React from "react";
import { Card, CardTitle } from "react-materialize";

const Pdf = props => (
  <div>
    <Card
      className="small"
      header={<CardTitle image={props.image}>{props.title}</CardTitle>}
      actions={[<a href={props.link}>Download</a>]}
    >
      {props.text}
    </Card>
  </div>
);

export default Pdf;
